import React from "react";
import { useSelector } from "react-redux";

// needed asssets

// svgs

// images
// needed styles
import styles from "./index.module.scss";

const EmploymentSubscriptionCard = ({
  header = "الاشتراك القياسي",
  content = "ستتمكن من الحصول على 100 سير مهنية لكل مهنة شهريا",
  price = "100",
  subType,
}) => {
  const {
    meta: { locale },
  } = useSelector((state) => state);

  return (
    <div className={styles["container"]}>
      <div className={styles["header"]}>
        <h3>{locale === "ar" ? subType?.title_ar : subType?.title_en}</h3>
      </div>
      <div className={styles["content"]}>
        <p>{content}</p>
      </div>
      <div className={styles["price"]}>
        <span>السعر {subType?.cost} ج.م</span>
      </div>
    </div>
  );
};

export default EmploymentSubscriptionCard;
